.btn.btn-small{
    padding: 6px 17px;
    font-size: 11px;
    letter-spacing: 1px;
}
.btn.btn-medium{
    padding: 8px 37px;
    font-size: 12px;
}
.btn.btn-large{
    padding: 12px 45px;
    font-size: 13px;
}

.btn {
    display: inline-block;
    padding: 0.3em 1em;
    text-decoration: none;
    color: _palette(font-base);
    border: solid 1px _palette(font-base);
    border-radius: 1em;
    transition: .4s;
    letter-spacing: normal;
}

.btn:hover {
    background:_palette(font-base);
    color: white;
}
