.content {
  margin-bottom: 4em;
  position: relative;

  .content-text {
    line-height: 1.875em;
    text-align: justify;
    text-justify: inter-ideograph;
  }

  .title {
    margin: 2em 0;
    position: relative;
    text-align: center;
  }


  .title:after {
      left: 50%;
      z-index: 1;
      width: 3em;
      height: .12em;
      content: " ";
      bottom: -1em;
      margin-left: -1.5em;
      text-align: center;
      position: absolute;
      background: _palette(ruby-base);
      box-sizing: border-box;
  }

  // top/news
  .swiper-container {
    width: 90%;
  }

  .content-news-wrapper {
    @media screen and (max-width: 35.5em) {}
  }
  .content-news {
    margin: $content-margin;
    background-color: white;
    max-height: 28em;

    .content-news-img {
      height: 17em;
      width: 100%;
      object-fit: cover;
      transform: scale(1.2);
      transition: .3s ease-in-out;
    }

    .contetn-news-img-wrapper {
      margin: 0;
      padding: 0;
      background: #fff;
      overflow: hidden;
    }

    .contetn-news-img-wrapper:hover img {
      -webkit-transform: scale();
      transform: scale(1);
    }


    .content-news-date {
      font-weight: bold;
    }

    .content-news-text-wrapper {
      height: 15em;
      position: relative;
    }

    .content-news-text {
      margin: 0;
    }

    a {
      position: absolute;
      bottom: 2em;
      right: 1em;
    }
  }

  .content-news-more {
    margin: 3em 0 0;
    text-align: center;
  }

  .content-news-facebook {
    margin: 3em 0 0;
    text-align: center;
    height: 500px;
  }


  // top/about

  .content-about {
    margin: $content-margin;
    .content-about-img {
      margin: 1em 0 0 0;
    }
    .content-about-text {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      padding: 0 2em;
      @media screen and (max-width: 35.5em) {
        padding: 0;
      }
    }
  }

  // top/about_us

  .content-mayor-comment {
    margin: $content-margin;
    .content-mayor-comment-img {
      margin: 1em 0 0 0;
    }
    .content-mayor-comment-text {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      padding: 0 2em;
      @media screen and (max-width: 35.5em) {
        padding: 0;
      }
    }
  }

  .content-communities {
    margin: 0 1.5em 1em;
  }

  .content-lab {
    margin: 0 1.5em 1em;

    .content-lab-col-2 {
      padding-right: 3em;
      @media screen and (max-width: 35.5em) {
        padding-right: 0;
      }
    }

    .content-lab-download-btn {
        margin: 2em auto;
    }

  }

  .content-education {
    padding: 2em;
    margin-bottom: 2em;
  }

}

.content-events {
  margin: 0 1em !important;
}

.content-events-swiper {
  margin: 0 1em 2em 1em;
}
