.loader-bg {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background: #fff;
  z-index: 9998;
}

.loader-skip {
  display: none;
  margin-top: 1.5em;
  color: _palette(font-base);
  font-size: 1.5em;
}

.loader-skip > p:hover {
  color: _palette(ruby-base);
  cursor: pointer;
}

.loader {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  margin-top: -100px;
  margin-left: -100px;
  text-align: center;
  color: #fff;
  z-index: 9999;

  svg#logo {
    width: 50%;
    margin-top: 10%
  }

  @media screen and (max-width: 35.5em) {
    svg#logo {
      width: 80%;
      margin-top: 8%
    }
  }
}
