footer {
  background: _palette(footer-color);
  position:absolute;
  width:100%;
  height: $footer-height;
  bottom:0;
  overflow:hidden;
  color: _palette(white);

  .footer-inner{
    padding: 1.5em 2em 0 2em;
    p {
      font-size: 0.9em;
      color: _palette(white);
    }

    img {
      margin-top: 1em;
      margin-bottom: .5em;
      width: 12em;
    }

    hr {
      border-color: gray;
    }

    .footer-name {
      margin: 0;
    }

    .footer-contact {
      float: right;
    }

    .footer-address {
      margin: 0;
    }

    .footer-tel {
      .title {
        width: 3em;
        display: flex;
        justify-content: space-between;
        float: left;
      }
      .tel {
        margin-left: 1em;
        float: left;
      }
      color: _palette(white);
    }

    .footer-email {
      .title {
        width: 3em;
        display: flex;
        justify-content: space-between;
        float: left;
      }
      .email {
        margin-left: 1em;
        float: left;
      }
      color: _palette(white);
    }

    @media screen and (max-width: 35.5em) {
      .footer-name {
        text-align: center;
      }

      .footer-contact {
        width: 20em;
        margin: 2em auto 0 auto;
        float: none
      }

      img {
        margin-top: 1em;
        margin-bottom: .5em;
        margin-left: auto;
        margin-right: auto;
        width: 12em;
      }
    }
  }
}

@media screen and (max-width: 35.5em) {
  footer {
    height: $footer-height * 2 !important;
  }
}
