.img-responsive {
    max-width: 100%;
    height: auto;
}

.pull-right {
  float: right;
}

.pull-left {
  float: left;
}

.clearfix:after {
    content:"";
    display:block;
    clear:both;
}

.red {
  color: red;
}

.bold {
  font-weight: bold;
}

.gothic {
  font-family: $font-family-gothic;
}

.mincho {
  font-family: $font-family-mincho;
}

$spaceamounts: (1, 2, 3, 4, 5, 6);
$sides: (top, bottom, left, right, all);

@each $space in $spaceamounts {
  @each $side in $sides {

    @if $side == 'all' {
      .m#{$space} {
        margin: #{$space}em;
      }

      .p#{$space} {
        padding: #{$space}em;
      }
    } @else {
      .m#{str-slice($side, 0, 1)}#{$space} {
        margin-#{$side}: #{$space}em;
      }

      .p#{str-slice($side, 0, 1)}#{$space} {
        padding-#{$side}: #{$space}em;
      }
    }
  }
}

.object-fit-img {
	object-fit: contain;
  width: 100%;
	font-family: 'object-fit: contain;';
}

.color-ruby {
  color: _palette(ruby-base);
}

.color-inverse {
  color: _palette(font-inverse);
}


#back-top {
	position: fixed;
	z-index: 9999;
	bottom: 0;
	right: 0;
  margin: 0;
}
#back-top a {
	width: 4em;
	height: 4em;
	display: block;
	text-align: center;
	font: 11px/100% Arial, Helvetica, sans-serif;
	text-transform: uppercase;
	text-decoration: none;
	color: white;
	background: gray;
	/* background color transition */
	-webkit-transition: 1s;
	-moz-transition: 1s;
	transition: 1s;
}
#back-top a:hover {
	background: _palette(ruby-base);
}


/* arrow icon (span tag) */
#back-top i {
	margin-top: .5em;
	font-size: 2em;
}

@media only screen
and (min-width : 35.5em)
and (max-width : 48em) {
#back-top {
	position: fixed;
	bottom: 0;
	right: 0;
}
#back-top a {
	width: 4em;
	height: 4em;
}
#back-top i {
	margin-top: .5em;
	font-size: 2em;
}
}
