.header {
  position: fixed;
  top: 0;
  height: 5em;
  display: block;
  width: 100%;
  z-index: 9999;
  background-color: _palette(white);

  p {
    margin: 1.5em 0 0 4.5em;
    font-weight: bold;
  }


  @media screen and (min-width: 64em) {
    display: none;
  }

  .header-img-wrapper {
    max-width: 50%;
    margin: 1em auto;
    text-align: center;
    img {
      width: 9em;
    }
  }
}
