.main {
  .cover {
    @media screen and (max-width: 64em) {
      .vegas-slide {
         margin-top: 5em;
      }
    }
    @mixin cover-background {}
    @mixin cover-about {
      padding: 3em 0 0 ;
      color: #fff;
      text-shadow: 3px 3px 0 grey, -1px -1px 0 grey, 1px -1px 0 grey, -1px 1px 0 grey, 1px 1px 0 grey;

      h1 {
        color: white;
        font-size: 3em;
      }

      h2 {
        color: white;
      }

      p {
        color: white;
      }
      @media screen and (min-width: 35.5em) {
        padding: 3em 0 0 0;
      }
      @media screen and (max-width: 35.5em) {
        h1 {
          font-size: 2em;
        }
      }
    }

    @mixin cover-about-inner {
      padding-left: 3em;
      @media screen and (max-width: 64em) {
        padding-top: 3em;
      }
    }


    .cover-top {
      @include cover-background;

      .cover-about-top {
        text-shadow: 3px 3px 0 grey, -1px -1px 0 grey, 1px -1px 0 grey, -1px 1px 0 grey, 1px 1px 0 grey;
        width: 100%;
        text-align: center;
        color: #fff;
        h2 {
          @include cover-about;
          margin-bottom: 4em;
        }

        .cover-about-top-inner {
          position: absolute;
          bottom: 0;
        }
      }
    }

    .cover-about-lab {
      @include cover-about
      position: absolute;
      top: 0;
    }

    .cover-about-lab-inner {
      @include cover-about-inner
    }


    .cover-about-collaboration {
      @include cover-about
      position: absolute;
      top: 0;
    }

    .cover-about-collaboration-inner {
      @include cover-about-inner;
    }

    .cover-communities {
      @include cover-background;
      color: _palette(white);

      .cover-about-communities-inner {
        @include cover-about-inner;
      }


    }
    @media screen and (max-width: 35.5em) {
      .cover-communities {
        background-position: left center;
        background-position-x: -12em;
      }
    }

    .cover-about-communities {
      @include cover-about
      width: 100%;
      height: 100%;
    }


    .cover-about-education {
      @include cover-about
      position: absolute;
      top: 0;
    }

    .cover-about-education-inner {
      @include cover-about-inner;
    }


    .cover-events {
      @include cover-background;
      color: _palette(white);

      .cover-about-events-inner {
        @include cover-about-inner
      }

      .cover-about-events-index {
        position:absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
      }

      .cover-about-event{
        text-align: center;
        margin-bottom: 5em;
        a {
          font-size: 1.5em;
          color: white;
          text-decoration: none;
        }
      }


      @media screen and (max-width: 35.5em) {
        .cover-about-event {
          margin-bottom: 3em;
        }

        .cover-about-event:last-child {
          margin-bottom: 10em;
        }
      }

    }
    @media screen and (max-width: 35.5em) {
      .cover-events {
        background-position: left center;
        background-position-x: -12em;
      }
    }

    .cover-about-events {
      @include cover-about
      width: 100%;
      height: 100%;
    }


    .cover-about-history {
      @include cover-about
      position: absolute;
      top: 0;
    }
    .cover-about-history-inner {
      @include cover-about-inner
    }

    .cover-about-about_us {
      @include cover-about
      position: absolute;
      top: 0;
    }
    .cover-about-about_us-inner {
      @include cover-about-inner
    }


    .cover-fog {
      background: rgba(235, 235, 235, 0.7);
    }

    .cover-inner {
      width: 100%;
      height: 100vh !important;
      min-height: 100%;
    }

    .cover-inner-short {
      display: -moz-flex;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 35vh;
      position: relative;
    }

    .cover-inner-medium {
      display: -moz-flex;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 50vh;
      position: relative;
    }

    .cover-inner-medium-long {
      display: -moz-flex;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 60vh;
      position: relative;
    }

    .cover-inner-long {
      display: -moz-flex;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 75vh;
      position: relative;
    }

    .cover-about-scroll-next {
      position:absolute;
      bottom: 0;

      text-align: center;
    }

    .cover-scroll-next {
      margin-bottom: 6.5em;
      a {
        z-index: 2;
        display: inline-block;
        transform: translate(0, -50%);
        text-decoration: none;
        padding-top: 3.5em;
        color: _palette(white);
      }

      a span::after {
        position: absolute;
        top: 50%;
        left: 50%;
        content: '';
        width: 16px;
        height: 16px;
        margin: -12px 0 0 -8px;
        border-left: 1px solid #fff;
        border-bottom: 1px solid #fff;
        transform: rotate(-45deg);
        box-sizing: border-box;
      }

      a span::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        content: '';
        width: 44px;
        height: 44px;
        box-shadow: 0 0 0 0 rgba(255,255,255,.1);
        border-radius: 100%;
        opacity: 0;
        box-sizing: border-box;
      }
    }
  }
}
