.menu {
  .menu-hidden {
    display: none;
  }
  background: _palette(white);
  border-right: 1px solid lightgray;
  box-sizing: border-box;
  margin-left: -$menu-width;
  width: $menu-width;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 1000;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  .menu-brand {
    margin: 4em auto 2em;
    width: 14em;

    img {
      width: 14em;
    }
    @media screen and (max-width: 64em) {
      img {
        display: none;
      }
    }
  }

  .menu-toggle-lang {
    margin-bottom: 3em;
    ul {
      text-align: center;
      padding: 0 2em;
      li {
        display: inline;
      }
    }
  }


  .menu-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    transform: translateY(0);
    transition: opacity 1s ease;
    margin-bottom: 1.5em;
    opacity: 1;
    width: 100%;

    .menu-inner-divider {
      width: 12em;
      border: 1px solid lightgray;
      margin: 0 auto;
    }

    nav {
      ul {
        padding: 0 1em;

        li {
          .menu-link {
            display: inline-block;
            position: relative;
            padding: 0.67em;
          }

          .menu-link span {
            transition: 0.6s;
            transition-delay: 0.2s;
          }

          .menu-link:after,
          .menu-link:before {
            content: '';
            position: absolute;
            top: 0.67em;
            left: 0;
            width: 100%;
            text-align: center;
            opacity: 0;
            transition: 0.4s,opacity 0.6s;
          }

          .menu-link:before {
            content: attr(data-hover);
            transform: translate(-10%,0);
          }

          .menu-link:after {
            content: attr(data-active);
            transform: translate(10%,0);
          }

          .menu-link:hover span {
            opacity: 0;
            transform: scale(0.3);
          }

          .menu-link:hover:before {
            opacity: 1;
            transform: translate(0,0);
            transition-delay: 0.3s;
          }
          margin: 0 1em;
          list-style: none;
          padding-bottom: 0.8em;
          //border-bottom: 2px solid _palette(font-lightest);
          text-align: center;

          a {
            color: _palette(font-lightest);
            font-size: 1em;
            font-weight: bold;
            text-decoration: none;
            text-transform: uppercase;
          }
        }

        li.active,
        li:hover {
          //border-bottom: 2px solid _palette(ruby-base);
          a {
            color: _palette(ruby-base);
            width: 10em;
          }
        }
      }
    }
    @media screen and (max-width: 64em) {
      .menu-inner-divider {
        display: none;
      }

      nav {
        ul {
          li {
            padding: 0;
          }
        }
      }
    }
  }
  @media screen and (max-width: 64em) {
    .menu-inner {
      margin-bottom: 1em;
    }
  }

  .menu-social {
    a {
      margin-left: 1em;
    }

    i {
      font-size: 2em;
      color: lightgray;
    }
    margin-left: 35%;
    margin-bottom: 3em;
  }

  .menu-toggle-simple-btn-wrapper {
    font-size: 2em;
    position: fixed;
    color: #c3c3c3;
    bottom: 0.6em;
    height: 1.5em;
    width: 10em;
    background: white;
    box-sizing: border-box;
    border-right: 1px solid lightgray;

    .menu-toggle-simple-btn {
      text-align: center;

      i:hover {
        color: _palette(ruby-base);
      }
    }
  }

  .menu-toggle-normal-btn-wrapper {
    font-size: 2em;
    position: fixed;
    color: #c3c3c3;
    bottom: 0.6em;
    height: 1.5em;
    width: 2em;
    box-sizing: border-box;

    .menu-toggle-normal-btn {
      text-align: center;

      i:hover {
        color: _palette(ruby-base);
      }
    }
  }
}

.menu-hamburger {
  position: fixed;
  display: block;
  width: 32px;
  height: 20px;
  top: 32px;
  left: 1em;
  margin-top: -10px;
  z-index: 99999;
  transition: all .4s;
  box-sizing: border-box;


  @media screen and (max-width: 35.5em) and (max-width: 48em) {
    top: 25px !important;
  }

  .menu-hamburger-description {
    position: absolute;
    color: black;
  }

  span {
    display: inline-block;
    transition: all 0.4s;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: gray;
    margin: 0 auto;
  }
}

.menu-hamburger.active {
  position: fixed;
  top: 32px;
  left: 1em !important;
}

.menu-hamburger span:nth-of-type(1) {
  top: 0;
}

.menu-hamburger span:nth-of-type(2) {
  top: 9px;
}

.menu-hamburger span:nth-of-type(3) {
  bottom: 0;
}

.menu-hamburger span:nth-of-type(1) {
  -webkit-animation: menu-ber01 0.75s forwards;
  animation: menu-ber01 0.75s forwards;
}

.menu-hamburger span:nth-of-type(2) {
  transition: all 0.25s 0.25s;
  opacity: 1;
}

.menu-hamburger span:nth-of-type(3) {
  -webkit-animation: menu-ber02 0.75s forwards;
  animation: menu-ber02 0.75s forwards;
}

.menu-hamburger.active span:nth-of-type(1) {
  -webkit-animation: active-menu-ber01 0.75s forwards;
  animation: active-menu-ber01 0.75s forwards;
}

.menu-hamburger.active span:nth-of-type(2) {
  opacity: 0;
}

.menu-hamburger.active span:nth-of-type(3) {
  -webkit-animation: active-menu-ber03 0.75s forwards;
  animation: active-menu-ber03 0.75s forwards;
}

@keyframes menu-ber01 {
  0% {
    transform: translateY(8px) rotate(45deg);
  }

  50% {
    transform: translateY(8px) rotate(0);
  }

  100% {
    transform: translateY(0) rotate(0);
  }
}
@keyframes menu-ber02 {
  0% {
    transform: translateY(-8px) rotate(-45deg);
  }

  50% {
    transform: translateY(-8px) rotate(0);
  }

  100% {
    transform: translateY(0) rotate(0);
  }
}
@keyframes active-menu-ber01 {
  0% {
    transform: translateY(0) rotate(0);
  }

  50% {
    transform: translateY(10px) rotate(0);
  }

  100% {
    transform: translateY(10px) rotate(45deg);
  }
}
@keyframes active-menu-ber03 {
  0% {
    transform: translateY(0) rotate(0);
  }

  50% {
    transform: translateY(-8px) rotate(0);
  }

  100% {
    transform: translateY(-8px) rotate(-45deg);
  }
}

@media (min-width: 64em) {
  .layout {
    padding-left: $menu-width;
    left: 0;
  }

  .menu {
    left: $menu-width;
  }

  .menu-hamburger {
    position: fixed;
    left: $menu-width;
    display: none;
  }

  .layout.active .menu-hamburger {
    left: $menu-width;
  }
}
@media (max-width: 64em) {
  .layout.active {
    position: relative;
    left: $menu-width;
  }
}
