.timeline {
  position: relative;
  margin: 0 auto;
  padding-top: 2em;
  padding-bottom: 4em; }
  .timeline h2 {
    text-align: center;
    position: relative;
    margin-bottom: 2em; }
  .timeline h2:after {
    left: 50%;
    z-index: 1;
    width: 3em;
    height: .12em;
    content: " ";
    bottom: -.7em;
    margin-left: -1.5em;
    text-align: center;
    position: absolute;
    background: #e66172;
    box-sizing: border-box; }

/* The actual timeline (the vertical ruler) */
.timeline::after {
  display: none; }
  @media screen and (min-width: 35.5em) {
    .timeline::after {
      content: '';
      position: absolute;
      width: 6px;
      background-color: lightgray;
      top: 0;
      bottom: 0;
      left: 50%;
      margin-left: -3px;
      z-index: -1;
      display: block; } }

/* Container around content */
.timeline-container {
  position: relative;
  background-color: inherit;
  width: 50%; }

/* The circles on the timeline */
.timeline-container::after {
  display: none; }
  @media screen and (min-width: 35.5em) {
    .timeline-container::after {
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      right: -17px;
      background-color: white;
      border: 4px solid #e66172;
      top: 15px;
      border-radius: 50%;
      z-index: 1;
      display: block; } }

/* Place the timeline-container to the left */
.timeline-left {
  left: 0; }

/* Place the timeline-container to the right */
.timeline-right {
  left: 50%; }

/* Add arrows to the left timeline-container (pointing right) */
.timeline-left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 16px;
  width: 0;
  z-index: 0;
  right: 1em;
  border: medium solid lightgray;
  border-width: 1em 0 1em 1em;
  border-color: transparent transparent transparent lightgray; }

/* Add arrows to the right timeline-container (pointing left) */
.timeline-right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 16px;
  width: 0;
  z-index: 0;
  left: 1em;
  border: medium solid lightgray;
  border-width: 1em 1em 1em 0;
  border-color: transparent lightgray transparent transparent; }

/* Fix the circle for timeline-containers on the right side */
.timeline-right::after {
  left: -16px; }

/* The actual content */
.timeline-content {
  padding: 20px 30px;
  border: 1px solid lightgray;
  background-color: white;
  position: relative;
  border-radius: 6px;
  margin: 0 2em; }
  .timeline-content table {
    margin-bottom: 1em; }
    .timeline-content table tbody tr {
      margin-bottom: 2em; }
      .timeline-content table tbody tr th {
        width: 3em;
        padding: 0.5em;
        vertical-align: top; }
      .timeline-content table tbody tr td {
        padding: 0.5em;
        max-width: 80%; }
        .timeline-content table tbody tr td p:nth-child(1) {
          margin-top: 0; }
      .timeline-content table tbody tr a {
        text-decoration: none; }

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 48em) {
  /* Place the timeline to the left */
  .timeline::after {
    left: 31px; }
  /* Full-width timeline-containers */
  .timeline-container {
    width: 100%;
    padding-left: 32px;
    padding-right: 25px; } }
  @media screen and (max-width: 48em) and (max-width: 35.5em) {
    .timeline-container {
      padding-left: 0;
      padding-right: 0; } }

@media screen and (max-width: 48em) {
  /* Make sure that all arrows are pointing leftwards */
  .timeline-container::before {
    left: 65px;
    border: solid lightgray;
    border-width: 1em 1em 1em 0;
    border-color: transparent lightgray transparent transparent; }
  /* Make sure all circles are at the same spot */
  .timeline-left::after,
  .timeline-right::after {
    left: 15px; }
  /* Make all right timeline-containers behave like the left ones */
  .timeline-right {
    left: 0; }
  .timeline-content {
    padding: 20px 30px;
    border: 1px solid #d3d3d3;
    position: relative;
    border-radius: 6px;
    margin: 0 3em 2em 3em; } }

