* {
  @font-face {
    font-family: YuGothicM;
    src: local("Yu Gothic Medium");
  }
  //font-family: $font-family-gothic
  font-family: $font-family-mincho;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  overflow-y: scroll;
  //font-size: calc(100vw * .03126);
  font-size: 16px; //calc(1em);
  @media screen and (max-width: 35.5em) {
    font-size: calc(.6em);
  }

  @media screen and (min-width: 35.5em) and (max-width: 35.5em) {
    font-size: calc(.6em);
  }
  @media screen and (min-width: 48em) {
    font-size: calc(.8em);
  }
  @media screen and (min-width: 64em) {
    font-size: calc(.8em);
  }
  @media screen and (min-width: 80em) {
    font-size: calc(1em);
  }
}

body {
  height: 100vh;
}

.last-content {
  padding-bottom: $footer-height;
}

@media screen and (max-width: 35.5em) {
  .last-content {
    padding-bottom: $footer-height * 2 !important;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: _palette(font-base);
}


.scroll-section {
  @media screen and (max-width: 35.5em) {
    //padding-top: 1em;
  }
}

.main {
  //background-color: #eff0f2;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  position: relative;
}

section.content {
  margin: 1% 0 0 0;
}


section.tilt {
  position: relative;
  overflow: hidden;
  padding: 80px 0;
}

section.tilt:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 120%;
  height: 85%;
  margin: 8% -10% 0;
  background: #ffefef;
  transform-origin: left center;
  transform: rotate(-4deg);
  z-index: -1;
}

.section_inner {
  box-sizing: boder-box;
  width: 100%;
  max-width: 640px;
  height: 100%;
  margin: 0 auto;
  padding: 100px 10px 120px;
  color: #fff;
  text-align: center;
}

